"use strict";

// Gobal vars
var pageW = window.innerWidth;
var mobResMax = 767;

var createHeaderDesktop = function createHeaderDesktop() {
  // New Html Tgas
  var menuContainerHtml = '<div class="menu_container"></div>';
  var IconLogin = '<i class="fas fa-sign-in-alt"></i>';
  var IconCart = '<i class="fas fa-shopping-cart"></i>';
  var MiniCartCont = '<div class="name-prod"></div>'; // Selectors for tags that already exists 

  var fullHeader = $('#cabecalho');
  var fixedHeader = $('#barraTopo');
  var mainHeader = $('.menu.superior');
  var mainMenu = $('.menu.superior .nivel-um');
  var logo = $('#cabecalho .logo');
  var faleConosco = $('.barra-inicial .canais-contato .hidden-phone');
  var topLinks = $('#cabecalho .acoes-conta');
  var minhaConta = $('#cabecalho .acoes-conta > li + li');
  var headerSearchCont = $('#cabecalho .conteudo-topo .inferior');
  var BemVindo = $('#cabecalho .bem-vindo');
  var BemVindoSpan = $('#cabecalho .bem-vindo > span');
  var CarrinhoLinkHeader = $('#cabecalho .inferior .hidden-phone');
  var MiniCartProd = $('.carrinho-interno > ul > li'); //Actions

  mainHeader.prepend(menuContainerHtml);
  $('.menu_container').prepend(logo).append(mainMenu);
  topLinks.append(faleConosco);
  CarrinhoLinkHeader.before(BemVindo);
  BemVindo.prepend(IconLogin);
  BemVindoSpan.html('LOGAR NA LOJA');
  MiniCartProd.each(function () {
    $('.preco-produto', this).prepend($('.nome-produto', this));
  });

  if (pageW >= mobResMax) {
    var lastScrollTop = 0;
    $(window).scroll(function (event) {
      var st = jQuery(this).scrollTop();

      if (st > fullHeader.height()) {
        fullHeader.addClass('fixed');

        if (st > lastScrollTop) {
          fullHeader.addClass('reduce');
        } else if (st < lastScrollTop) {
          fullHeader.removeClass('reduce');
        }
      } else if (st < lastScrollTop) {
        fullHeader.removeClass('fixed');
      }

      ;
      lastScrollTop = st;
    });
  }
};

var createHeaderMobile = function createHeaderMobile() {
  //New Html Tags
  var BtMobMenu = $('<li class="mobMenu"><i class="fas fa-bars"></i><i class="fas fa-times"></i></li>');
  var mobSearch = $('<li class="mobSearch"><i class="fas fa-search-plus"></i><i class="fas fa-search-minus"></i></li>');
  var mobAcoesConta = $('<li class="mobAcoesConta"></li>'); //Already existing tag selector

  var mobileBar = $('.atalhos-mobile > ul');
  var mobMenuButton = $('.atalho-menu');
  var mobSearchBar = $('.busca');
  var acoesConta = $('#cabecalho .acoes-conta');
  var mobMenu = $('#cabecalho .nivel-um'); // Actions

  if (pageW <= mobResMax) {
    mobileBar.prepend(BtMobMenu);
    mobileBar.append(mobSearch);
    mobMenu.append(mobAcoesConta);
    mobAcoesConta.append(acoesConta);
    $('.mobMenu').click(function () {
      $(this).toggleClass('active');
      mobMenuButton.click();
    });
    $('.mobSearch').click(function () {
      $(this).toggleClass('active');
      $('.logo').toggleClass('active');
      mobSearchBar.toggleClass('active');
    });
    $('.nivel-um .com-filho').click(function () {
      $(this).toggleClass('open');
    });
  }
};

var setSelectedCategory = function setSelectedCategory() {
  var bodyClasses = $('body').attr('class').trim().split(' ');
  var getClass = bodyClasses.find(function (bc) {
    return bc.includes('categoria-');
  });

  if (getClass !== undefined) {
    var categoryId = $(".categoria-id-".concat(getClass.split('categoria-').pop()));
    categoryId.addClass('active');
  }
};

